<template>
  <div>
    <!-- 认证页面 -->
    <div v-if="currentPage === 'login' || currentPage === 'register' || currentPage === 'reset'">
      <LoginPage v-if="currentPage === 'login'" @goto-register="goToRegister" @goto-reset="goToReset" @login-success="handleLoginSuccess" />
      <RegisterPage v-else-if="currentPage === 'register'" @goto-login="goToLogin" />
      <ResetPasswordPage v-else-if="currentPage === 'reset'" @goto-login="goToLogin" />
    </div>
    
    <!-- 主应用页面 -->
    <Navigation 
      v-else 
      :current-module="currentModule" 
      :username="username" 
      :user-role="userRole"
      @navigate="handleNavigate" 
      @logout="handleLogout"
    >
      <!-- 根据当前模块显示对应的内容 -->
      <ResourceRequest 
        v-if="currentModule === 'resource' || currentModule === 'allRequests'"
        :user-role="userRole"
      />
      <ResourceMonitoring 
        v-else-if="currentModule === 'monitor'"
        :user-role="userRole"
      />
      <ServerManagement 
        v-else-if="currentModule === 'servers'"
        :user-role="userRole"
      />
      <AdminRequests 
        v-else-if="currentModule === 'adminRequests'"
        :user-role="userRole"
      />
    </Navigation>
  </div>
</template>

<script>
// 导入认证相关组件
import LoginPage from './components/Login.vue'
import RegisterPage from './components/Register.vue'
import ResetPasswordPage from './components/ResetPassword.vue'

// 导入主应用组件
import Navigation from './components/Navigation.vue'
import ServerManagement from './components/ServerManagement.vue'
import ResourceRequest from './components/ResourceRequest.vue'
import ResourceMonitoring from './components/ResourceMonitoring.vue'
import AdminRequests from './components/AdminRequests.vue'

export default {
  name: 'App',
  components: {
    // 认证组件
    LoginPage,
    RegisterPage,
    ResetPasswordPage,
    
    // 主应用组件
    Navigation,
    ResourceRequest,
    ResourceMonitoring,
    ServerManagement,
    AdminRequests
  },
  data() {
    return {
      currentPage: 'login', // 默认显示登录页面
      currentModule: 'resource', // 默认显示资源申请模块
      username: '管理员', // 默认用户名
      userRole: '' // 用户角色: 'user' 或 'admin'
    }
  },
  methods: {
    // 认证页面切换方法
    goToRegister() {
      this.currentPage = 'register'
    },
    
    goToReset() {
      this.currentPage = 'reset'
    },
    
    goToLogin() {
      this.currentPage = 'login'
    },
    
    // 登录成功处理
    handleLoginSuccess(userInfo) {
      //todo  查找是否有这个用户
      this.username = userInfo.username || '管理员'
      this.userRole = userInfo.role || 'user'
      
      // 根据角色设置默认页面
      if (this.userRole === 'admin') {
        this.currentPage = 'main'
        this.currentModule = 'monitor' // 管理员默认显示服务器监控
      } else {
        this.currentPage = 'main'
        this.currentModule = 'resource' // 用户默认显示资源申请
      }
    },
    
    // 导航处理
    handleNavigate(module) {
      this.currentModule = module
    },
    
    // 退出登录处理
    handleLogout() {
      this.currentPage = 'login'
      // 在实际应用中，这里会清除登录状态、token等
      console.log('用户已退出登录')
    }
  }
}
</script>

<style>
/* 导入公共样式 */
@import './assets/styles.css';

#app {
  width: 100%;
  height: 100vh;
}
</style>