<template>
  <main-layout page-title="资源申请">
    <div class="resource-request-content">
      <!-- 标签切换 -->
      <div class="tabs">
        <button 
          class="tab-button" 
          v-if="userRole === 'user'"
          :class="{ active: activeTab === 'new-request' }"
          @click="switchTab('new-request')"
        >
          新建申请
        </button>
        <button 
          class="tab-button" 
          v-if="userRole === 'user'"
          :class="{ active: activeTab === 'my-requests' }"
          @click="switchTab('my-requests')"
        >
          我的申请
        </button>
        <button 
          class="tab-button" 
          v-if="userRole === 'admin'"
          :class="{ active: activeTab === 'all-requests' }"
          @click="switchTab('all-requests')"
        >
          全部申请
        </button>
      </div>
      
      <!-- 新建申请表单 -->
      <div v-if="activeTab === 'new-request'" class="request-form-container">
        <form @submit.prevent="handleSubmitRequest" class="request-form">
          <!-- 申请基本信息 -->
          <div class="form-section">
            <h3>申请基本信息</h3>
            
            <div class="form-row">
              <div class="form-group">
                <label for="resourceType">资源类型 <span class="required">*</span></label>
                <select id="resourceType" v-model="formData.resourceType" class="form-select" required>
                  <option value="">请选择资源类型</option>
                  <option value="server">服务器</option>
                  <option value="server">显卡</option>
                </select>
              </div>
              
              <div class="form-group">
                <label for="requestTitle">申请标题 <span class="required">*</span></label>
                <input type="text" id="requestTitle" v-model="formData.requestTitle" class="form-input" placeholder="请输入申请标题" required>
              </div>
            </div>
            
            <div class="form-row">
              <div class="form-group">
                <label for="urgencyLevel">紧急程度 <span class="required">*</span></label>
                <select id="urgencyLevel" v-model="formData.urgencyLevel" class="form-select" required>
                  <option value="normal">普通</option>
                  <option value="urgent">紧急</option>
                  <option value="very-urgent">非常紧急</option>
                </select>
              </div>
              
              <div class="form-group">
                <label for="expectedDate">期望完成日期 <span class="required">*</span></label>
                <input type="date" id="expectedDate" v-model="formData.expectedDate" class="form-input" required>
              </div>
            </div>
          </div>
          
          <!-- 资源需求详情 -->
          <div class="form-section">
            <h3>资源需求详情</h3>
            
            <div class="form-row">
              <div class="form-group full-width">
                <label for="serverId">GPU服务器 <span class="required">*</span></label>
                <select id="serverId" v-model="formData.serverId" class="form-select" required @change="onServerChange">
                  <option value="">请选择服务器</option>
                  <option v-for="server in availableServers" :key="server.id" :value="server.id">
                    {{ server.name }} (IP: {{ server.ip }}, CPU使用率: {{ server.cpuUsage }}%, 内存使用率: {{ server.memoryUsage }}%)
                  </option>
                </select>
              </div>
            </div>
            
            <div class="form-row">
              <div class="form-group">
                <label for="gpuIndex">起始显卡编号 <span class="required">*</span></label>
                <select id="gpuIndex" v-model="formData.gpuIndex" class="form-select" required :disabled="!formData.serverId">
                  <option value="">请选择起始显卡</option>
                  <option v-for="gpu in availableGpus" :key="gpu" :value="gpu">
                    显卡 #{{ gpu }}
                  </option>
                </select>
              </div>
              
              <div class="form-group">
                <label for="gpuCount">显卡数量 <span class="required">*</span></label>
                <input type="number" id="gpuCount" v-model="formData.gpuCount" class="form-input" min="1" :max="availableGpuCount" placeholder="如：1" required :disabled="!formData.serverId">
              </div>
            </div>

            
            <div class="form-row">
              <div class="form-group full-width">
                <label for="resourceDescription">资源需求描述 <span class="required">*</span></label>
                <textarea id="resourceDescription" v-model="formData.resourceDescription" class="form-textarea" rows="4" placeholder="请详细描述您的GPU使用场景和需求" required></textarea>
              </div>
            </div>
          </div>
          
          <!-- 评分体系 -->
          <div class="form-section">
            <h3>评分体系</h3>
            
            <div class="form-row">
              <div class="form-group">
                <label for="projectContributionScore">项目贡献度评分 <span class="required">*</span></label>
                <input type="number" id="projectContributionScore" v-model="formData.projectContributionScore" class="form-input" min="1" max="10" placeholder="1-10分" required>
              </div>
              
              <div class="form-group">
                <label for="researchAchievementScore">科研成果评分 <span class="required">*</span></label>
                <input type="number" id="researchAchievementScore" v-model="formData.researchAchievementScore" class="form-input" min="1" max="10" placeholder="1-10分" required>
              </div>
            </div>
            
            <div class="form-row">
              <div class="form-group">
                <label for="resourceUsageEfficiencyScore">资源使用效率评分 <span class="required">*</span></label>
                <input type="number" id="resourceUsageEfficiencyScore" v-model="formData.resourceUsageEfficiencyScore" class="form-input" min="1" max="10" placeholder="1-10分" required>
              </div>
              
              <div class="form-group">
                <label for="comprehensiveScore">综合得分</label>
                <input type="text" id="comprehensiveScore" :value="comprehensiveScore.toFixed(1)" class="form-input" disabled placeholder="系统自动计算">
              </div>
            </div>
          </div>
          
          <!-- 联系方式 -->
          <div class="form-section">
            <h3>联系方式</h3>
            
            <div class="form-row">
              <div class="form-group">
                <label for="contactPerson">联系人 <span class="required">*</span></label>
                <input type="text" id="contactPerson" v-model="formData.contactPerson" class="form-input" placeholder="请输入联系人姓名" required>
              </div>
              
              <div class="form-group">
                <label for="contactPhone">联系电话 <span class="required">*</span></label>
                <input type="tel" id="contactPhone" v-model="formData.contactPhone" class="form-input" placeholder="请输入联系电话" required>
              </div>
              
              <div class="form-group">
                <label for="contactEmail">电子邮箱 <span class="required">*</span></label>
                <input type="email" id="contactEmail" v-model="formData.contactEmail" class="form-input" placeholder="请输入电子邮箱" required>
              </div>
            </div>
          </div>
          
          <!-- 表单操作按钮 -->
          <div class="form-actions">
            <button type="button" class="cancel-button" @click="resetForm">取消</button>
            <button type="submit" class="submit-button" :disabled="isSubmitting">{{ isSubmitting ? '提交中...' : '提交申请' }}</button>
          </div>
        </form>
      </div>
      
      <!-- 我的申请记录 -->
      <div v-else-if="activeTab === 'my-requests'" class="my-requests-container">
        <!-- 搜索和筛选 -->
        <div class="search-filter">
          <div class="search-input-wrapper">
            <input type="text" v-model="searchKeyword" class="search-input" placeholder="搜索申请标题或ID">
            <span class="search-icon">🔍</span>
          </div>
          
          <div class="filter-wrapper">
            <select v-model="statusFilter" class="filter-select">
              <option value="">全部状态</option>
              <option value="pending">待审核</option>
              <option value="approved">已批准</option>
              <option value="rejected">已拒绝</option>
              <option value="processing">处理中</option>
              <option value="completed">已完成</option>
            </select>
          </div>
        </div>
        
        <!-- 申请列表 -->
        <div class="request-list">
          <div class="request-item" v-for="request in requestsList" :key="request.id">
            <div class="request-header">
              <div class="request-title">
                <h4>{{ request.title }}</h4>
                <span class="request-id">申请ID: {{ request.id }}</span>
              </div>
              <div class="request-status" :class="request.status">
                {{ getStatusText(request.status) }}
              </div>
            </div>
            
            <div class="request-content">
              <div class="request-info">
                <div class="info-item">
                  <span class="info-label">资源类型:</span>
                  <span class="info-value">{{ getResourceTypeText(request.resourceType) }}</span>
                </div>
                <div class="info-item">
                  <span class="info-label">申请时间:</span>
                  <span class="info-value">{{ request.applyTime }}</span>
                </div>
                <div class="info-item" v-if="request.allocatedServer">
                  <span class="info-label">分配服务器:</span>
                  <span class="info-value allocated-server">{{ request.allocatedServer.name }} ({{ request.allocatedServer.ip }})</span>
                </div>
                <div class="info-item">
                  <span class="info-label">期望完成:</span>
                  <span class="info-value">{{ request.expectedDate }}</span>
                </div>
              </div>
              
              <div class="request-description">
                {{ request.description }}
              </div>
            </div>
            
            <div class="request-actions">
              <button class="view-button" @click="viewRequestDetail(request.id)">查看详情</button>
              <button v-if="request.status === 'pending'" class="cancel-button" @click="cancelRequest(request.id)">取消申请</button>
            </div>
          </div>
        </div>
        
        <!-- 分页 -->
        <div class="pagination">
          <div class="page-info">
            显示 {{ (currentPage - 1) * pageSize + 1 }} - {{ Math.min(currentPage * pageSize, totalRequests) }} 条，共 {{ totalRequests }} 条
          </div>
          <div class="page-controls">
            <button class="page-btn" :disabled="currentPage === 1" @click="changePage(currentPage - 1)">上一页</button>
            <span class="page-num">{{ currentPage }} / {{ Math.ceil(totalRequests / pageSize) }}</span>
            <button class="page-btn" :disabled="currentPage === Math.ceil(totalRequests / pageSize)" @click="changePage(currentPage + 1)">下一页</button>
          </div>
        </div>
      </div>
      
      <!-- 管理员：全部申请记录 -->
      <div v-else-if="activeTab === 'all-requests'" class="all-requests-container">
        <!-- 搜索和筛选 -->
        <div class="search-filter">
          <div class="search-input-wrapper">
            <input type="text" v-model="adminSearchKeyword" class="search-input" placeholder="搜索申请标题、ID或申请人">
            <span class="search-icon">🔍</span>
          </div>
          
          <div class="filter-wrapper">
            <select v-model="adminStatusFilter" class="filter-select">
              <option value="">全部状态</option>
              <option value="pending">待审核</option>
              <option value="approved">已批准</option>
              <option value="rejected">已拒绝</option>
              <option value="processing">处理中</option>
              <option value="completed">已完成</option>
            </select>
          </div>
        </div>
        
        <!-- 申请列表 -->
        <div class="request-list">
          <div class="request-item" v-for="request in filteredAllRequests" :key="request.id">
            <div class="request-header">
              <div class="request-title">
                <h4>{{ request.title }}</h4>
                <span class="request-id">申请ID: {{ request.id }}</span>
                <span class="request-applicant">申请人: {{ request.applicant }}</span>
                <span class="request-score" v-if="request.score">综合评分: {{ request.score }}</span>
              </div>
              <div class="request-status" :class="request.status">
                {{ getStatusText(request.status) }}
              </div>
            </div>
            
            <div class="request-content">
              <div class="request-info">
                <div class="info-item">
                  <span class="info-label">资源类型:</span>
                  <span class="info-value">{{ getResourceTypeText(request.resourceType) }}</span>
                </div>
                <div class="info-item">
                  <span class="info-label">申请时间:</span>
                  <span class="info-value">{{ request.applyTime }}</span>
                </div>
                <div class="info-item" v-if="request.allocatedServer">
                  <span class="info-label">分配服务器:</span>
                  <span class="info-value allocated-server">{{ request.allocatedServer.name }} ({{ request.allocatedServer.ip }})</span>
                </div>
              </div>
              
              <div class="request-actions">
                <button 
                  class="primary-button" 
                  v-if="request.status === 'pending'"
                  @click="allocateServer(request.id)"
                >
                  分配服务器
                </button>
                <button 
                  class="secondary-button" 
                  v-if="request.status === 'pending'"
                  @click="rejectRequest(request.id)"
                >
                  拒绝申请
                </button>
                <button 
                  class="secondary-button"
                  @click="viewRequestDetails(request.id)"
                >
                  查看详情
                </button>
              </div>
            </div>
          </div>
        </div>
        
        <!-- 分页 -->
        <div class="pagination">
          <div class="page-info">
            显示 {{ (adminCurrentPage - 1) * adminPageSize + 1 }} - {{ Math.min(adminCurrentPage * adminPageSize, filteredAllRequests.length) }} 条，共 {{ filteredAllRequests.length }} 条
          </div>
          <div class="page-controls">
            <button class="page-btn" :disabled="adminCurrentPage === 1" @click="changeAdminPage(adminCurrentPage - 1)">上一页</button>
            <span class="page-num">{{ adminCurrentPage }} / {{ Math.ceil(filteredAllRequests.length / adminPageSize) }}</span>
            <button class="page-btn" :disabled="adminCurrentPage === Math.ceil(filteredAllRequests.length / adminPageSize)" @click="changeAdminPage(adminCurrentPage + 1)">下一页</button>
          </div>
        </div>
      </div>
    </div>
  </main-layout>
</template>

<script>
import MainLayout from './MainLayout.vue'

export default {
  components: {
    MainLayout
  },
  name: 'ResourceRequest',
  props: {
    userRole: {
      type: String,
      default: 'user'
    }
  },
  data() {
    return {
      activeTab: 'new-request', // 'new-request' 或 'my-requests'
      
      // 表单数据
      formData: {
        resourceType: '',
        requestTitle: '',
        urgencyLevel: 'normal',
        expectedDate: '',
        cpuCount: '',
        memorySize: '',
        storageSize: '',
        // GPU相关需求
        serverId: '',
        gpuIndex: '',
        gpuCount: 1,
        resourceDescription: '',
        // 新的评分体系
        projectContributionScore: '',
        researchAchievementScore: '',
        resourceUsageEfficiencyScore: '',
        contactPerson: '',
        contactPhone: '',
        contactEmail: ''
      },
      
      isSubmitting: false,
      
      // 我的申请列表相关数据
      requestsList: [
        {
          id: 'REQ20231120001',
          title: '测试环境服务器申请',
          resourceType: 'server',
          status: 'approved',
          applyTime: '2023-11-20 09:30',
          expectedDate: '2023-11-25',
          description: '申请用于新项目测试的服务器资源，配置要求：8核CPU，16GB内存，500GB存储空间。'
        },
        {
          id: 'REQ20231115002',
          title: '数据存储扩容申请',
          resourceType: 'storage',
          status: 'processing',
          applyTime: '2023-11-15 14:20',
          expectedDate: '2023-11-20',
          description: '因业务增长，现有存储资源不足，需要扩容1TB存储空间。'
        },
        {
          id: 'REQ20231110003',
          title: '专业软件许可证申请',
          resourceType: 'software',
          status: 'pending',
          applyTime: '2023-11-10 11:45',
          expectedDate: '2023-11-15',
          description: '申请3套专业设计软件的临时许可证，用于新产品研发。'
        },
        {
          id: 'REQ20231105004',
          title: '网络带宽升级申请',
          resourceType: 'network',
          status: 'rejected',
          applyTime: '2023-11-05 16:10',
          expectedDate: '2023-11-10',
          description: '因国际业务需求，需要将现有网络带宽从100Mbps升级到500Mbps。'
        },
        {
          id: 'REQ20231101005',
          title: 'GPU服务器申请',
          resourceType: 'server',
          status: 'completed',
          applyTime: '2023-11-01 10:00',
          expectedDate: '2023-11-05',
          description: '申请用于AI模型训练的GPU服务器，配置要求：4张V100 GPU，64GB内存。'
        }
      ],
      
      searchKeyword: '',
      statusFilter: '',
      typeFilter: '',
      currentPage: 1,
      pageSize: 10,
      totalRequests: 5,
      
      // 管理员相关数据
      allRequestsList: [
        {
          id: 'REQ20231120001',
          title: '测试环境服务器申请',
          resourceType: 'server',
          status: 'approved',
          applyTime: '2023-11-20 09:30',
          expectedDate: '2023-11-25',
          description: '申请用于新项目测试的服务器资源，配置要求：8核CPU，16GB内存，500GB存储空间。',
          applicant: '张三',
          score: 9.2,
          allocatedServer: {
            name: 'GPU服务器1',
            ip: '192.168.1.101'
          }
        },
        {
          id: 'REQ20231115002',
          title: '数据存储扩容申请',
          resourceType: 'storage',
          status: 'processing',
          applyTime: '2023-11-15 14:20',
          expectedDate: '2023-11-20',
          description: '因业务增长，现有存储资源不足，需要扩容1TB存储空间。',
          applicant: '李四',
          score: 8.5
        },
        {
          id: 'REQ20231110003',
          title: '专业软件许可证申请',
          resourceType: 'software',
          status: 'pending',
          applyTime: '2023-11-10 11:45',
          expectedDate: '2023-11-15',
          description: '申请3套专业设计软件的临时许可证，用于新产品研发。',
          applicant: '王五',
          score: 7.8
        },
        {
          id: 'REQ20231105004',
          title: '网络带宽升级申请',
          resourceType: 'network',
          status: 'rejected',
          applyTime: '2023-11-05 16:10',
          expectedDate: '2023-11-10',
          description: '因国际业务需求，需要将现有网络带宽从100Mbps升级到500Mbps。',
          applicant: '赵六',
          score: 6.5
        },
        {
          id: 'REQ20231101005',
          title: 'GPU服务器申请',
          resourceType: 'server',
          status: 'pending',
          applyTime: '2023-11-01 10:00',
          expectedDate: '2023-11-05',
          description: '申请用于AI模型训练的GPU服务器，配置要求：4张V100 GPU，64GB内存。',
          applicant: '孙七',
          score: 9.5
        }
      ],
      
      // 可用服务器列表 - 7台服务器，每台8张显卡
      availableServers: [
        {
          id: 1,
          name: 'GPU服务器1',
          ip: '192.168.1.101',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 20,
          memoryUsage: 25
        },
        {
          id: 2,
          name: 'GPU服务器2',
          ip: '192.168.1.102',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 25,
          memoryUsage: 30
        },
        {
          id: 3,
          name: 'GPU服务器3',
          ip: '192.168.1.103',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 30,
          memoryUsage: 20
        },
        {
          id: 4,
          name: 'GPU服务器4',
          ip: '192.168.1.104',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 15,
          memoryUsage: 18
        },
        {
          id: 5,
          name: 'GPU服务器5',
          ip: '192.168.1.105',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 35,
          memoryUsage: 32
        },
        {
          id: 6,
          name: 'GPU服务器6',
          ip: '192.168.1.106',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 28,
          memoryUsage: 22
        },
        {
          id: 7,
          name: 'GPU服务器7',
          ip: '192.168.1.107',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 22,
          memoryUsage: 26
        }
      ],
      
      // 管理员搜索和筛选
      adminSearchKeyword: '',
      adminStatusFilter: '',
      adminCurrentPage: 1,
      adminPageSize: 10
    }
  },
  methods: {
    // 切换标签
    switchTab(tabName) {
      this.activeTab = tabName
    },
    
    // 提交申请
    async handleSubmitRequest() {
      try {
        // 表单验证
        if (!this.validateForm()) {
          return
        }
        
        this.isSubmitting = true
        // 模拟API请求
        await new Promise(resolve => setTimeout(resolve, 1500))
        
        // 计算综合评分
        const projectContributionScore = parseInt(this.formData.projectContributionScore)
        const researchAchievementScore = parseInt(this.formData.researchAchievementScore)
        const resourceUsageEfficiencyScore = parseInt(this.formData.resourceUsageEfficiencyScore)
        
        // 计算综合得分：项目贡献度(40%) + 科研成果(30%) + 资源使用效率(30%)
        const totalScore = projectContributionScore * 0.4 + researchAchievementScore * 0.3 + resourceUsageEfficiencyScore * 0.3
        
        // 获取选择的服务器信息
        const server = this.selectedServer
        
        // 模拟成功响应
        alert(`申请提交成功！\n申请ID：REQ${Date.now().toString().slice(-8)}\n您的申请评分为：${totalScore.toFixed(1)}\n申请GPU信息：\n- 服务器：${server.name} (${server.ip})\n- 显卡范围：#${this.formData.gpuIndex} 至 #${parseInt(this.formData.gpuIndex) + parseInt(this.formData.gpuCount) - 1}\n- 显卡数量：${this.formData.gpuCount}张`)
        this.resetForm()
      } catch (error) {
        console.error('提交申请失败：', error)
        alert('提交申请失败，请稍后重试')
      } finally {
        this.isSubmitting = false
      }
    },
    
    // 表单验证
      validateForm() {
        // 验证基本信息
        if (!this.formData.resourceType || !this.formData.requestTitle) {
          alert('请填写申请基本信息')
          return false
        }
        
        // 验证GPU相关信息
        if (!this.formData.serverId || !this.formData.gpuIndex || !this.formData.gpuCount) {
          alert('请填写完整的GPU需求信息')
          return false
        }
        
        // 验证评分（1-10分）
        const scores = [
          this.formData.projectContributionScore,
          this.formData.researchAchievementScore,
          this.formData.resourceUsageEfficiencyScore
        ]
        
        for (const score of scores) {
          const numScore = parseInt(score)
          if (isNaN(numScore) || numScore < 1 || numScore > 10) {
            alert('评分必须是1-10之间的数字')
            return false
          }
        }
      
      // 验证联系方式
      if (!this.formData.contactPerson || !this.formData.contactPhone || !this.formData.contactEmail) {
        alert('请填写完整的联系方式')
        return false
      }
      
      return true
    },
    
    // 重置表单
      resetForm() {
        this.formData = {
          resourceType: '',
          requestTitle: '',
          urgencyLevel: 'normal',
          expectedDate: '',
          cpuCount: '',
          memorySize: '',
          storageSize: '',
          // GPU相关需求
          serverId: '',
          gpuIndex: '',
          gpuCount: 1,
          resourceDescription: '',
          // 新的评分体系
          projectContributionScore: '',
          researchAchievementScore: '',
          resourceUsageEfficiencyScore: '',
          contactPerson: '',
          contactPhone: '',
          contactEmail: ''
        }
      },
      
      // 处理服务器选择变化
      onServerChange() {
        // 当服务器改变时，重置显卡选择
        this.formData.gpuIndex = ''
        this.formData.gpuCount = 1
      },
    
    // 查看申请详情
    viewRequestDetail(requestId) {
      console.log('查看申请详情：', requestId)
      // 实际应用中，这里会跳转到申请详情页或打开详情弹窗
    },
    
    // 取消申请
    cancelRequest(requestId) {
      if (confirm('确定要取消此申请吗？')) {
        console.log('取消申请：', requestId)
        // 实际应用中，这里会调用取消申请的API
        alert('申请已取消')
      }
    },
    
    // 切换分页
    changePage(pageNum) {
      if (pageNum > 0 && pageNum <= Math.ceil(this.totalRequests / this.pageSize)) {
        this.currentPage = pageNum
      }
    },
    
    // 获取状态文本
    getStatusText(status) {
      const statusMap = {
        pending: '待审核',
        approved: '已批准',
        rejected: '已拒绝',
        processing: '处理中',
        completed: '已完成'
      }
      return statusMap[status] || status
    },
    
    // 获取资源类型文本
    getResourceTypeText(type) {
      const typeMap = {
        server: '服务器',
        storage: '存储资源',
        network: '网络资源',
        software: '软件许可证',
        other: '其他资源'
      }
      return typeMap[type] || type
    },
    
    // 管理员功能：分配服务器
    allocateServer(requestId) {
      // 查找请求
      const request = this.allRequestsList.find(item => item.id === requestId)
      if (!request) return
      
      // 查找可用的服务器（按照CPU和内存使用率低的优先）
      const availableServer = this.availableServers
        .sort((a, b) => (a.cpuUsage + a.memoryUsage) - (b.cpuUsage + b.memoryUsage))
        .find(server => server.availableGpus.length > 0)
      
      if (availableServer) {
        // 分配服务器
        request.allocatedServer = availableServer
        request.status = 'approved'
        alert(`已成功分配服务器: ${availableServer.name}`)
      } else {
        alert('当前无可用服务器资源，请稍后再试')
      }
    },
    
    // 管理员功能：拒绝申请
    rejectRequest(requestId) {
      if (confirm('确定要拒绝该申请吗？')) {
        const request = this.allRequestsList.find(item => item.id === requestId)
        if (request) {
          request.status = 'rejected'
          alert('申请已拒绝')
        }
      }
    },
    
    // 管理员功能：查看申请详情
    viewRequestDetails(requestId) {
      console.log('查看申请详情：', requestId)
      // 实际应用中，这里会跳转到申请详情页或打开详情弹窗
    },
    
    // 管理员分页
    changeAdminPage(pageNum) {
      if (pageNum > 0 && pageNum <= Math.ceil(this.filteredAllRequests.length / this.adminPageSize)) {
        this.adminCurrentPage = pageNum
      }
    }
  },
  computed: {
      // 获取当前选择的服务器
      selectedServer() {
        return this.availableServers.find(server => server.id === parseInt(this.formData.serverId))
      },
      
      // 计算综合得分
      comprehensiveScore() {
        const projectContributionScore = parseInt(this.formData.projectContributionScore)
        const researchAchievementScore = parseInt(this.formData.researchAchievementScore)
        const resourceUsageEfficiencyScore = parseInt(this.formData.resourceUsageEfficiencyScore)
        
        // 检查所有评分是否都已填写且为有效数字
        if (isNaN(projectContributionScore) || isNaN(researchAchievementScore) || isNaN(resourceUsageEfficiencyScore)) {
          return 0
        }
        
        // 计算综合得分：项目贡献度(40%) + 科研成果(30%) + 资源使用效率(30%)
        return projectContributionScore * 0.4 + researchAchievementScore * 0.3 + resourceUsageEfficiencyScore * 0.3
      },
      
      // 获取可用显卡列表
    availableGpus() {
      if (!this.selectedServer) {
        return []
      }
      return this.selectedServer.availableGpus
    },
    
    // 获取可用显卡数量
    availableGpuCount() {
      if (!this.selectedServer) {
        return 0
      }
      return this.selectedServer.availableGpus.length
    },
    
    // 过滤后的申请列表
    filteredRequests() {
      return this.requestsList.filter(request => {
        const matchesSearch = !this.searchKeyword || 
          request.title.includes(this.searchKeyword) || 
          request.id.includes(this.searchKeyword)
        const matchesStatus = !this.statusFilter || request.status === this.statusFilter
        const matchesType = !this.typeFilter || request.resourceType === this.typeFilter
        return matchesSearch && matchesStatus && matchesType
      })
    },
    
    // 过滤后的全部申请列表(管理员)
    filteredAllRequests() {
      return this.allRequestsList.filter(request => {
        const matchesSearch = !this.adminSearchKeyword || 
          request.title.includes(this.adminSearchKeyword) || 
          request.id.includes(this.adminSearchKeyword) || 
          request.applicant.includes(this.adminSearchKeyword)
        const matchesStatus = !this.adminStatusFilter || request.status === this.adminStatusFilter
        return matchesSearch && matchesStatus
      })
    }
  },
  watch: {
    // 当搜索条件变化时，重置为第一页
    searchKeyword() {
      this.currentPage = 1
    },
    statusFilter() {
      this.currentPage = 1
    },
    typeFilter() {
      this.currentPage = 1
    },
    
    // 管理员搜索条件变化时，重置为第一页
    adminSearchKeyword() {
      this.adminCurrentPage = 1
    },
    adminStatusFilter() {
      this.adminCurrentPage = 1
    }
  }
}
</script>

