<template>
  <div class="auth-container">
    <div class="auth-header">
      <div class="header-content">
        <img src="../assets/logo.png" alt="服务器logo" class="logo" />
        <h1>服务器资源监测管理系统</h1>
      </div>
    </div>
    <div class="auth-box">
      <h2>账号密码登录</h2>
      <form @submit.prevent="handleLogin" class="auth-form">
        <div class="form-group">
          <div class="input-wrapper phone-input-wrapper">
            <select v-model="countryCode" class="country-code">
              <option value="+86">+86</option>
            </select>
            <input
              type="tel"
              v-model="phone"
              placeholder="请输入手机号"
              class="phone-input"
              required
              pattern="[0-9]{11}"
            />
          </div>
        </div>
        
        <div class="form-group">
          <div class="input-wrapper password-input-wrapper">
            <input
              type="password"
              v-model="password"
              placeholder="请输入密码"
              class="password-input"
              required
            />
          </div>
          <button type="button" class="forgot-password" @click="handleForgotPassword">忘记密码?</button>
        </div>
        
        <div class="form-group">
          <div class="input-wrapper role-select-wrapper">
            <select v-model="userRole" class="role-select" required>
              <option value="">请选择登录角色</option>
              <option value="user">普通用户</option>
              <option value="admin">管理员</option>
            </select>
          </div>
        </div>
        
        <button type="submit" class="primary-button login-button" :disabled="isLoading">
          <span v-if="!isLoading">登录</span>
          <span v-else>登录中...</span>
        </button>
        
        <div class="alternative-login">
          <button type="button" class="secondary-button verify-login" @click="switchToVerifyLogin">验证码登录</button>
        </div>
      </form>
      
      <div class="quick-register">
        <span>还没有账号？</span>
        <button type="button" class="link-button register-button" @click="handleRegister">快速注册</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoginPage',
  data() {
    return {
      countryCode: '+86',
      phone: '',
      password: '',
      userRole: '',
      isLoading: false,
      isVerifyLogin: false
    }
  },
  methods: {
    async handleLogin() {
      // 简单的表单验证
      if (!this.phone || !this.password || !this.userRole) {
        alert('请填写完整的登录信息并选择角色');
        return;
      }
      
      if (this.phone.length !== 11) {
        alert('请输入正确的手机号码');
        return;
      }
      
      this.isLoading = true;
      
      try {
        // 模拟登录请求
        await new Promise(resolve => setTimeout(resolve, 1500));
        
        // 这里可以替换为实际的登录API调用
        console.log('登录成功:', {
          countryCode: this.countryCode,
          phone: this.phone,
          password: this.password,
          userRole: this.userRole
        });
        
        // 登录成功后的处理，触发登录成功事件
        alert('登录成功！');
        // 发送登录成功事件，传递用户名和角色
        this.$emit('login-success', { username: this.phone, role: this.userRole });
      } catch (error) {
        console.error('登录失败:', error);
        alert('登录失败，请重试');
      } finally {
        this.isLoading = false;
      }
    },
    
    handleForgotPassword() {
      // 忘记密码逻辑
      this.$emit('goto-reset');
    },
    
    
    handleRegister() {
      // 注册逻辑
      this.$emit('goto-register');
    }
  }
}
</script>

<style scoped>
/* 导入公共样式 */
@import '../assets/styles.css';

/* 登录页面特定样式 */
/* 调整选择框的大小 - 使用最具体的选择器并增加严格的样式规则 */
.auth-form .form-group .input-wrapper.phone-input-wrapper .country-code {
  padding: 0 8px !important;
  min-width: 100px !important;
  width: 100px !important;
  max-width: 100px !important;
  font-size: 14px !important;
  height: 36px !important;
  background-color: #ffffff !important;
  border: none !important;
  border-radius: 0 !important;
  margin: 0 !important;
  flex-shrink: 0;
}

/* 调整手机号输入框的样式以匹配 */
.auth-form .form-group .input-wrapper.phone-input-wrapper .phone-input {
  padding: 0 12px !important;
  height: 36px !important;
  border: none !important;
  border-radius: 0 !important;
  flex: 1 !important;
}

/* 调整输入框容器的高度和样式 */
.auth-form .form-group .input-wrapper.phone-input-wrapper {
  height: 36px !important;
  display: flex !important;
  align-items: center !important;
  border: 1px solid #d9d9d9 !important;
  border-radius: 4px !important;
  overflow: hidden !important;
}

/* 角色选择框容器样式 */
.auth-form .form-group .input-wrapper.role-select-wrapper {
  height: 36px !important;
  display: flex !important;
  align-items: center !important;
  border: 1px solid #d9d9d9 !important;
  border-radius: 4px !important;
  overflow: hidden !important;
}

/* 角色选择框样式 */
.auth-form .form-group .input-wrapper.role-select-wrapper .role-select {
  width: 100% !important;
  padding: 0 12px !important;
  height: 36px !important;
  font-size: 14px !important;
  background-color: #ffffff !important;
  border: none !important;
  border-radius: 0 !important;
  outline: none !important;
}
</style>