<template>
  <div class="navigation-container">
    <div class="sidebar">
      <div class="sidebar-header">
        <img src="../assets/logo.png" alt="服务器logo" class="sidebar-logo" />
        <h1 class="sidebar-title">服务器资源监测管理系统</h1>
      </div>
      <ul class="nav-menu">
        <!-- 用户导航菜单 -->
        <li 
          class="nav-item" 
          v-if="userRole === 'user'"
          :class="{ active: currentModule === 'resource' }"
          @click="navigateTo('resource')"
        >
          <i class="icon">📋</i>
          <span>资源申请</span>
        </li>
        <li 
          class="nav-item" 
          v-if="userRole === 'user'"
          :class="{ active: currentModule === 'servers' }"
          @click="navigateTo('servers')"
        >
          <i class="icon">🖥️</i>
          <span>服务器列表</span>
        </li>
        
        <!-- 管理员导航菜单 -->
        <li 
          class="nav-item" 
          v-if="userRole === 'admin'"
          :class="{ active: currentModule === 'monitor' }"
          @click="navigateTo('monitor')"
        >
          <i class="icon">📈</i>
          <span>服务器监控</span>
        </li>
        <li 
          class="nav-item" 
          v-if="userRole === 'admin'"
          :class="{ active: currentModule === 'allRequests' }"
          @click="navigateTo('allRequests')"
        >
          <i class="icon">📋</i>
          <span>申请列表</span>
        </li>
      </ul>
    </div>
    <div class="main-content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NavigationBar',
  props: {
    currentModule: {
      type: String,
      default: 'resource'
    },
    username: {
      type: String,
      default: '管理员'
    },
    userRole: {
      type: String,
      default: 'user'
    }
  },
  methods: {
    navigateTo(module) {
      this.$emit('navigate', module);
    },
    handleLogout() {
      this.$emit('logout');
    }
  }
}
</script>

<style scoped>
/* 导入公共样式 */
@import '../assets/styles.css';

.navigation-container {
  display: flex;
  height: 100vh;
  background-color: #f5f7fa;
  position: relative;
  overflow: hidden;
}

.sidebar {
  width: 200px;
  background-color: white;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  overflow-y: auto;
  z-index: 2;
}

.sidebar-header {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 16px 20px;
  color: #1890ff;
  position: fixed;
  top: 0;
  left: 0;
  width: 160px;
  z-index: 3;
}

.sidebar-logo {
  width: 24px;
  height: 24px;
  border-radius: 4px;
}

.sidebar-title {
  font-size: 16px;
  font-weight: 600;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.nav-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 80px;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  cursor: pointer;
  transition: all 0.3s;
  border-left: 3px solid transparent;
  font-size: 15px;
}

.nav-item:hover {
  background-color: #f0f7ff;
  color: #1890ff;
}

.nav-item.active {
  background-color: #e6f7ff;
  color: #1890ff;
  border-left-color: #1890ff;
}

.icon {
  font-size: 18px;
}

.main-content {
  flex: 1;
  margin-left: 200px;
  overflow-y: auto;
  background-color: #f5f7fa;
  position: relative;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .sidebar {
    transform: translateX(-100%);
    transition: transform 0.3s ease;
  }
  
  .sidebar.open {
    transform: translateX(0);
  }
  
  .main-content {
    margin-left: 0;
  }
}

/* 响应式设计 */
@media (max-width: 768px) {
  .sidebar {
    width: 60px;
  }
  
  .nav-item span {
    display: none;
  }
  
  .main-content {
    margin-left: 60px;
  }
  
  .header h1 {
    font-size: 16px;
  }
}
</style>