<template>
  <div class="auth-container">
    <div class="auth-header">
      <div class="header-content">
        <img src="../assets/logo.png" alt="服务器logo" class="logo" />
        <h1>服务器资源监测管理系统</h1>
      </div>
    </div>
    <div class="auth-box">
      <h2>重置密码</h2>
      <form @submit.prevent="handleResetPassword" class="auth-form">
        <div class="form-group">
          <div class="input-wrapper phone-input-wrapper">
            <select v-model="countryCode" class="form-select country-code">
              <option value="+86">+86</option>
              <option value="+1">+1</option>
              <option value="+44">+44</option>
              <!-- 可以添加更多国家代码 -->
            </select>
            <input
              type="tel"
              v-model="phone"
              placeholder="请输入手机号"
              class="form-input phone-input"
              required
              pattern="[0-9]{11}"
            />
          </div>
        </div>
        
        <div class="form-group">
          <div class="input-wrapper password-input-wrapper">
            <input
              type="password"
              v-model="newPassword"
              placeholder="输入新密码"
              class="form-input password-input"
              required
            />
          </div>
        </div>
        
        <div class="form-group">
          <div class="input-wrapper password-input-wrapper">
            <input
              type="password"
              v-model="confirmPassword"
              placeholder="确认新密码"
              class="form-input password-input"
              required
            />
          </div>
        </div>
        
        <button type="submit" class="primary-button login-button" :disabled="isLoading">
          <span v-if="!isLoading">确定重置</span>
          <span v-else>重置中...</span>
        </button>
      </form>
      
      <div class="quick-register">
        <button type="button" class="link-button register-button" @click="goToLogin">需要登录？</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResetPasswordPage',
  data() {
    return {
      countryCode: '+86',
      phone: '',
      newPassword: '',
      confirmPassword: '',
      isLoading: false
    }
  },
  methods: {
    async handleResetPassword() {
      // 简单的表单验证
      if (!this.phone || !this.newPassword || !this.confirmPassword) {
        alert('请填写完整的重置信息');
        return;
      }
      
      if (this.phone.length !== 11) {
        alert('请输入正确的手机号码');
        return;
      }
      
      if (this.newPassword.length < 6) {
        alert('密码长度不能少于6位');
        return;
      }
      
      if (this.newPassword !== this.confirmPassword) {
        alert('两次输入的密码不一致');
        return;
      }
      
      this.isLoading = true;
      
      try {
        // 模拟重置密码请求
        await new Promise(resolve => setTimeout(resolve, 1500));
        
        // 这里可以替换为实际的重置密码API调用
        console.log('密码重置成功:', {
          countryCode: this.countryCode,
          phone: this.phone,
          newPassword: this.newPassword
        });
        
        // 重置成功后的处理，例如，例如跳转到登录页
        alert('密码重置成功！请使用新密码登录');
        this.goToLogin();
      } catch (error) {
        console.error('密码重置失败:', error);
        alert('密码重置失败，请重试');
      } finally {
        this.isLoading = false;
      }
    },
    
    goToLogin() {
      // 跳转到登录页面
      this.$emit('goto-login');
    }
  }
}
</script>

<style scoped>
/* 导入公共样式 */
@import '../assets/styles.css';

/* 重置密码页面特定样式 */
</style>